import React, { Component } from 'react'
import { observer, inject, } from 'mobx-react'
import { ListGroup, ListGroupItem, Badge } from 'reactstrap';

import './PostsIndexBox.scss'
class PostsIndexBox extends Component {
    render() {
        const { headerListId, scrollToElement}   = this.props;
        let list = headerListId.map(( item, key )=> {
            let str = item.split("_")[0];
            return (
                <ListGroupItem onClick ={() => scrollToElement(item)}
                    key ={key}>
                    {str}
                </ListGroupItem>
            )
        });
        return (
            <div className = {'postsindexbox_conatainer'} >
                <Badge color="info">Header</Badge>
                <ListGroupItem>
                    {list}    
                </ListGroupItem> 
            </div>
        )
    }
}

export default inject(({ board }) => ({
    headerListId :board.headerListId,
    scrollToElement : board.scrollToElement
 }))(observer(PostsIndexBox));