import React, { Component, Fragment } from 'react'
import style from './BoardCardView.module.css'
import { Route,withRouter } from 'react-router-dom';
import history from '../../../history'
import * as boardApi from '../../../lib/api/boardApi'
import Popup from "reactjs-popup";
import Prompt from '../Prompt'
import { withCookies, Cookies } from 'react-cookie';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import WithAuth from '../../WithAuth';
import StyledLink from '../../StyleComponent/StyledLink/'
import { Button, Badge } from 'reactstrap';
import moment from 'moment'
import { observer, inject, } from 'mobx-react'
import {  action } from 'mobx';
import Preview from '../../Preview'
import PostsIndexBox from '../PostsIndexBox'


import fontawesome from '@fortawesome/fontawesome'
import { FontAwesomeIcon  } from '@fortawesome/react-fontawesome';
import { faEye, faPencilAlt, faCalendarAlt} from '@fortawesome/fontawesome-free-solid'

fontawesome.library.add(faEye, faPencilAlt, faCalendarAlt);

class BoardCardView extends Component {
 
    state ={
        postsJemok : '',
        postsNaeYong : '',
        primeYN : '',
        regDate : '',
        views : '',
        noticeStart : '',
        noticeEnd : '',
        postsNumber : this.props.match.params.postsNumber,
        open : false
    }

    openModal = () =>{
        this.setState({ 
            open: true
        })
    }
    //open  
    closeModal = () => {
        this.setState({
            open: false
        })
    }


    handleDeleteBoard = async() =>{
        this.openModal();
    }
    confirmDelete = async() => {
        console.log('confirm')
        const { postsNumber } =this.state;
        try{
            const response = await boardApi.deletePosts(1, postsNumber);
            history.push("/board/notice");

        }catch(e){
            console.log(e)
        }
    }



    handleEditBoard = () => {
        const {match} = this.props;
        console.log()
        /*    */
        
        window.history.push("/board/notice/editPosts/" +`${match.params.postsNumber}`);
    }



    componentDidMount(){
        const { getPosts } = this.props;
        console.log('parseInt(this.props.match.params.postsNumber)', this.props.match)
        getPosts(this.props);
        //this.handleNameChange('cookieSet')
    }
    componentDidUpdate(){
        this.props.findHeadingTag();
    }
   

render() {    
    const {
        match,
        boardViewContent,
        findHeadingTag
     } = this.props;
     
    return (
    <div className = {style.cardviewWraapper}>
      <div className = {style.panel}>
        <div className = {style.panelHeading}>
            <div>
                <h4>
                    <Badge color="secondary">    :#{ match.params.postsNumber }</Badge>
                    { boardViewContent.primeYN === 'Y' ?  ( <Badge style ={{marginLeft :'3px'}} 
                                                color="success">  </Badge> ) : ('') }
                </h4> 
            </div>

            <div>
                <FontAwesomeIcon 
                icon="pencil-alt" 
                size="1x" 
                color="black"
                >
                </FontAwesomeIcon>
                <span style ={{fontSize:'20px'}}> {boardViewContent.memUsername}</span>
            </div>
            <div>
                <FontAwesomeIcon 
                    icon="eye" 
                    size="1x" 
                    color="black"
                >
                </FontAwesomeIcon>
                 <span style ={{fontSize:'15px'}}> {boardViewContent.views} </span>
            </div>
            <div>
                <FontAwesomeIcon 
                    icon="calendar-alt" 
                    size="1x" 
                    color="black"
                >
                </FontAwesomeIcon>
                {/* primeYN === 'Y' ?
                <span style ={{fontSize:'15px'}}> {moment(noticeStart).format('YYYY-MM-DD')} - {moment(noticeEnd).format('YYYY-MM-DD')} </span>
                :
                <span style ={{fontSize:'15px'}}> {moment(noticeStart).format('YYYY-MM-DD')} - {moment(noticeEnd).format('YYYY-MM-DD')} </span>
                */}
                {/*<span style ={{fontSize:'15px'}}> {moment(boardViewContent.noticeStart).format('YYYY-MM-DD')} - {moment(noticeEnd).format('YYYY-MM-DD')} </span>*/}
            </div>
        </div>
        <div id = "content-body"  className = {style.panelBody}>
            <div className = { style.headPostsName} >  
                <h2 className = {style.title} > { boardViewContent.postsJemok } </h2>
                <hr></hr>
            </div>
            <div className = { style.contentText}> 
                <Preview parsedText = {boardViewContent.postsNaeYong} />
                { /*ReactHtmlParser(boardViewContent.postsNaeYong) */}
            </div>

            </div>
        <div className = {style.boardButtonCompo}>
            <StyledLink exact to ={"/board/notice/editPosts/" +`${match.params.postsNumber}`}>
                <Button outline color="primary">
                     
                </Button>{' '}
            </StyledLink>
            <Button outline color="danger" onClick = {this.handleDeleteBoard}>
                
            </Button>{' '}
            <StyledLink to={"/board/notice"}>
                <Button outline color="info">
                
                </Button>{' '}
            </StyledLink>
        </div>
        {/*
        <Prompt open = { open }
                closeModal = {this.closeModal}
                confirmDelete = {this.confirmDelete}/>
        */}
      </div>
      <PostsIndexBox/>
      </div>
    )
  }
}



export default inject(({ board }) => ({
    findHeadingTag :board.findHeadingTag,
    getPosts : board.getPosts,
    boardViewContent : board.boardViewContent,
 }))(observer(WithAuth(BoardCardView)));

