import React from 'react'
import WordItem from '../WordItem'
import { inject, observer } from 'mobx-react'; // 

const items = [
    // {
    //     author: '',
    //     text : '1',
    // },
    // {
    //     author: '',
    //     text : '2',
    // },
    // {
    //     author: '',
    //     text : '3',
    // },
    // {
    //     author: '',
    //     text : '4',
    // }
]


// **** onPut  
const GoodWordItemList = () => {
    console.log('!!!!!!!oodWordItemList ', items)
    const itemList = items.map(item => (
      <WordItem item = {item}  key={item.name} />
    ));
    return <div>{itemList}</div>;
  };
  
// **** inject, observer  
//   observer  observable    
// forceUpdate    
//   .

export default inject(({ word }) => ({
    items: word.goodWordItems,
  }))(observer(GoodWordItemList));

