import React, { Component, Fragment } from 'react'
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import { Button } from 'reactstrap';
import fontawesome from '@fortawesome/fontawesome'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { fa, faUserCircle } from '@fortawesome/fontawesome-free-solid'
import WithAuth from '../../WithAuth';
import style from './MyPage.module.css';
import PasswordChange from '../PasswordChange'


import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import * as memberApi from '../../../lib/api/memberApi';
import helpers from '../../../lib/helpers';


fontawesome.library.add(faUserCircle);
const styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
      width: 250,
    },
    dense: {
      marginTop: 19,
    },
    menu: {
      width: 200,
    },
  });

  
class MyPage extends Component {
     
    state = {
      passwordChangeSet : false,
      gubun : '',
      name : '',
      sosok : '',
      email : ''
    }

    componentDidMount(){
      this.getMemberInfo();
    }

    getMemberInfo = async() => {
      const { confirm } = this.props;
      //onsole.log('confirm ', confirm.mem_email)
      try{
        let response = await memberApi.getMemberInfo(confirm.mem_email);
        let memberInfo = response.data[0]
        console.log(memberInfo)
        this.setState({
          gubun : memberInfo.GB_CD_NM,
          email : memberInfo.MEM_EMAIL,
          sosok : memberInfo.MEM_SOSOK_NM,
          name  : memberInfo.MEM_USERNAME
        })
      }catch(e){
        console.log(e)
      }
    }

    updateMemberSosok = async() => {
      const { sosok } =this.state;
      const { confirm } = this.props;
      //onsole.log('confirm ', confirm.mem_email)
      console.log(sosok);
      try{
        let response = await memberApi.updateMemberSosok(sosok, confirm.mem_email);
        console.log('res ', response)
        if ( response.data.code === 200 ){
          toast.success(' .', {
            position: "top-center",
            autoClose: 1500,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            });
        }else{
          toast.error(' .', {
            position: "top-center",
            autoClose: 1500,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            });
        }
        
      }catch(e){
        console.log(e)
      }
    }
  
    passwordChangeSetting = () => {
      this.setState({
        passwordChangeSet : !this.state.passwordChangeSet
      })
    }


    handleChange = (e) =>{
      console.log(e)
      this.setState({
        sosok : e.target.value,
      });
    }
    render() {
      console.log(this.state.sosok)
        const { classes } = this.props;
        const { name, 
                sosok, 
                email, 
                gubun, 
                passwordChangeSet}  = this.state;
        return (
          <div className = {style.myPageContainer}>

          <div className = {style.outer}>
          <div className = {style.inner}>
          <div className = {style.centered}>
          <ToastContainer
              position="top-center"
              autoClose={3000}
              hideProgressBar={true}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnVisibilityChange
              draggable
              pauseOnHover
          /> 
            <FontAwesomeIcon 
            icon="user-circle" 
            size="5x" 
            color="#868e96"
            />
            <h4> 
            {name}
            </h4>
             
            <h4> 
            {gubun}
            </h4>
            <hr/>
            {!passwordChangeSet ?  ( 
              <Fragment>
            <div>
              <TextField
                id="outlined-name"
                label=""
                className={classes.textField}
                value={email}
                //onChange={this.handleChange('name')}
                margin="normal"
                variant="outlined"
                InputProps={{
                  readOnly: true,
                }}
              />
            </div>
            <div>
              <TextField
                id="outlined-name"
                label=""
                className={classes.textField}
                value={name}
                //onChange={this.handleChange('name')}
                margin="normal"
                variant="outlined"
                InputProps={{
                  readOnly: true,
                }}
              />
            </div>
            <form>
              <TextField
                id="outlined-name"
                label=''
                className={classes.textField}
                value={sosok}
                onChange={this.handleChange}
                margin="normal"
                variant="outlined"
              />
            </form>
              <div className>
                <Button outline color="info"
                        onClick = {this.updateMemberSosok} >
                   
                </Button>{' '}

                <NavLink to ={'/board/notice'}>
                  <Button outline color="info">
                    
                  </Button>{' '}
                </NavLink>

                <Button outline color="primary"
                        onClick = {this.passwordChangeSetting}>
                   
                </Button>{' '}
              </div>
              </Fragment>
            ):
            (
              <PasswordChange passwordChangeSetting = {this.passwordChangeSetting}
                              toast ={toast}/>
            )}
              </div>
              </div>

              </div>
          
          </div>
          
        )
  }
}
export default WithAuth(withStyles(styles)(MyPage));
