import React, { Component, Fragment } from 'react'
import style from './WriteBoardCard.module.css'
import { Form, Input, Button } from 'reactstrap';
import WithAuth from '../../WithAuth';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import _ from 'lodash'
import Editer from '../../Editer'
import { observer, inject, } from 'mobx-react'


/*  ,    */
class WriteBoardCard extends Component { 

     componentDidMount(){
         console.log('componentDidMount')
        const { makeSelectOption } = this.props;
        makeSelectOption();
     }
    
     componentWillUnmount(){
        console.log('componentWillUnmount')
        this.props.init();
     }

      render() {
        let { 
            handlePostsNameChange,
            writePosts,
            selectHandleChange,
            selectThumbnail,
            confirm, 
            options, 
            selectedOption,
            isSearchable,
            isClearable,
            thumbnailDisplayUrl,
        } = this.props
        return (
            <div className = {style.container}>
                <div>
                <hr/>
                    <Select
                        className = {style.boardSelect}
                        value={selectedOption}
                        isSearchable ={isSearchable}
                        isClearable  = {isClearable}
                        onChange={selectHandleChange}
                        options={options}
                        placeholder ={' '}
                    />
       
                    <hr/>
                    <Form className ={style.jemokContainer}>
                        <Input className = {style.jemokItem}
                                onChange = {handlePostsNameChange} 
                                placeholder = ' .'/>
                    </Form>
                </div>
                <hr/>
                <div className ={ style.thumbnailContainer}>
                    <Button color="secondary"
                            onClick = { selectThumbnail} >
                            { _.isNil(thumbnailDisplayUrl ) ?  ' ' : '' } 
                    </Button>
                </div>

                <hr/>

                <div className = {style.editerContainer}>
                    <div className= {style.thumbnailContainer}>
                        { _.isNil(thumbnailDisplayUrl ) ?  ''  : 
                        ( 
                        <Fragment>
                            <div>    </div>
                            <img style={{width: ``, height: '300px'}} src={thumbnailDisplayUrl}/>
                        </Fragment>
                            
                        )
                    }
                    </div>
                    <Editer confirm = {confirm} />     
                </div>
                 
                <div className ={style.buttonContainer}>
                    <Button color="primary"  onClick = {() => writePosts(confirm)}> </Button>{' '}
                    <Link to={'/board/notice'}>
                        <Button color="secondary"></Button>{' '}
                    </Link>
                </div>
            </div>
        )
      }
    
}

export default inject(({ board }) => ({
    makeSelectOption : board.makeSelectOption,
    handlePostsNameChange : board.handlePostsNameChange,
    selectHandleChange : board.selectHandleChange,
    writePosts : board.writePosts,
    postJemok: board.postJemok,
    options : board.options,
    selectedOption : board.selectedOption,
    isSearchable : board.isSearchable,
    isClearable : board.isClearable,
    selectThumbnail : board.selectThumbnail,
    thumbnailDisplayUrl : board.thumbnailDisplayUrl,
    thumbnailImg : board.thumbnailImg,
    init : board.init
  }))(observer(WithAuth(WriteBoardCard)));
