import React, { Component } from 'react'
import style from './BoardView.module.css'
import { Route,withRouter } from 'react-router-dom';
import history from '../../../history'
import * as boardApi from '../../../lib/api/boardApi'
import Popup from "reactjs-popup";
import Prompt from '../Prompt'
import { withCookies, Cookies } from 'react-cookie';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import WithAuth from '../../WithAuth';
import StyledLink from '../../StyleComponent/StyledLink/'
import { Button, Badge } from 'reactstrap';
import moment from 'moment'



import fontawesome from '@fortawesome/fontawesome'
import { FontAwesomeIcon  } from '@fortawesome/react-fontawesome';
import { faEye, faPencilAlt, faCalendarAlt} from '@fortawesome/fontawesome-free-solid'

fontawesome.library.add(faEye, faPencilAlt, faCalendarAlt);

class BoardView extends Component {
 
    state ={
        postsJemok : '',
        postsNaeYong : '',
        primeYN : '',
        regDate : '',
        views : '',
        noticeStart : '',
        noticeEnd : '',
        postsNumber : this.props.match.params.postsNumber,
        open : false
    }

    openModal = () =>{
        this.setState({ 
            open: true
        })
    }
    //open  
    closeModal = () => {
        this.setState({
            open: false
        })
    }


    handleDeleteBoard = async() =>{
        this.openModal();
    }
    confirmDelete = async() => {
        console.log('confirm')
        const { postsNumber } =this.state;
        try{
            const response = await boardApi.deletePosts(1, postsNumber);
            history.push("/board/notice");

        }catch(e){
            console.log(e)
        }
    }



    handleEditBoard = () => {
        const {match} = this.props;
        console.log()
        /*    */
        
        window.history.push("/board/notice/editPosts/" +`${match.params.postsNumber}`);
    }



    componentDidMount(){
        console.log('parseInt(this.props.match.params.postsNumber)', this.props.match)
        this.getPosts();
        
        //this.handleNameChange('cookieSet')
    }

    getPosts = async() => {
        const { match } = this.props;
        let num = match.params.postsNumber;
        console.log(match.url)
        try{
            boardApi.setViewsUp(num); //   
            const response = await boardApi.getPosts(num);
            let postContent = response.data[0];
            let MEM_USERNAME = postContent.MEM_USERNAME;
            let POSTS_JEMOK = postContent.POSTS_JEMOK;
            let POSTS_NAEYONG = postContent.POSTS_NAEYONG;
            let PRIME_YN = postContent.PRIME_YN;
            let REG_DATE = postContent.REG_DATE;
            let VIEWS = postContent.VIEWS + 1;
            let NOTICE_START = postContent.NOTICE_START;
            let NOTICE_END = postContent.NOTICE_END;
            console.log(POSTS_JEMOK)
            this.setState({
                memUsername : MEM_USERNAME,
                postsJemok : POSTS_JEMOK,
                postsNaeYong : POSTS_NAEYONG,
                primeYN : PRIME_YN,
                regDate : REG_DATE,
                views : VIEWS,
                noticeStart : NOTICE_START,
                noticeEnd : NOTICE_END,
            })

        }catch(e){
            console.log(e)
        }
    }

render() {    
    const {
        match,
     } = this.props;
     const { 
        postsJemok,
        postsNaeYong,
        memUsername,
        primeYN,
        regDate,
        views,
        noticeStart,
        noticeEnd,
        open
     } = this.state;

     
    return (
      <div className = {style.panel}>
        <div className = {style.panelHeading}>
            <div>
                <h4>
                    <Badge color="secondary">    :#{ match.params.postsNumber }</Badge>
                    { primeYN === 'Y' ?  ( <Badge style ={{marginLeft :'3px'}} 
                                                color="success">  </Badge> ) : ('') }
                </h4> 
            </div>

            <div>
                <FontAwesomeIcon 
                icon="pencil-alt" 
                size="1x" 
                color="black"
                >
                </FontAwesomeIcon>
                <span style ={{fontSize:'20px'}}> {memUsername}</span>
            </div>
            <div>
                <FontAwesomeIcon 
                    icon="eye" 
                    size="1x" 
                    color="black"
                >
                </FontAwesomeIcon>
                 <span style ={{fontSize:'15px'}}> {views} </span>
            </div>
            <div>
                <FontAwesomeIcon 
                    icon="calendar-alt" 
                    size="1x" 
                    color="black"
                >
                </FontAwesomeIcon>
                {/* primeYN === 'Y' ?
                <span style ={{fontSize:'15px'}}> {moment(noticeStart).format('YYYY-MM-DD')} - {moment(noticeEnd).format('YYYY-MM-DD')} </span>
                :
                <span style ={{fontSize:'15px'}}> {moment(noticeStart).format('YYYY-MM-DD')} - {moment(noticeEnd).format('YYYY-MM-DD')} </span>
                */}
                <span style ={{fontSize:'15px'}}> {moment(noticeStart).format('YYYY-MM-DD')} - {moment(noticeEnd).format('YYYY-MM-DD')} </span>
            </div>
        </div>
        <div id = "content-body"  className = {style.panelBody}>
            <div className = { style.headPostsName} >  
                <h2 className = {style.title} > { postsJemok } </h2>
                <hr></hr>
            </div>
            <div className = { style.contentText}> 
                { ReactHtmlParser(postsNaeYong) }
            </div>

            </div>
        <div className = {style.boardButtonCompo}>
            <StyledLink exact to ={"/board/notice/editPosts/" +`${match.params.postsNumber}`}>
                <Button outline color="primary">
                     
                </Button>{' '}
            </StyledLink>
            <Button outline color="danger" onClick = {this.handleDeleteBoard}>
                
            </Button>{' '}
            <StyledLink to={"/board/notice"}>
                <Button outline color="info">
                
                </Button>{' '}
            </StyledLink>
        </div>
        <Prompt open = { open }
                closeModal = {this.closeModal}
                confirmDelete = {this.confirmDelete}/>

      </div>
    )
  }
}
export default WithAuth(BoardView);