import { observable, action, computed } from 'mobx';

export default class WordStore {
  @observable goodWordItems = [];
  @observable selectedItems = [];

  @observable author;
  @observable text;
  // **** 
  constructor(root) {
    this.root = root;
  }

  @action  
  putWord = (author, text) => {
    console.log('author ' , author , ' text ' , text )
    let lengthGoodWord = this.goodWordItems.length;
    lengthGoodWord += 1
    console.log('lengthGoodWord ' ,lengthGoodWord)
    this.goodWordItems.push({
        author,
        text ,
        key : lengthGoodWord,
    });
    console.log('goodWordItems ' ,this.goodWordItems)
  }

  @action
  handleAuthorChange = (e) => {
    this.author = e.target.value
  };

  @action
  handleTextChange = (e) => {
    this.text = e.target.value
  };

   




  @action
  put = (name, price) => {
    const { number } = this.root.counter;
    //  
    const exists = this.selectedItems.find(item => item.name === name);
    if (!exists) {
      //    .
      this.selectedItems.push({
        name,
        price,
        count: number,
      });
      return;
    }
    //   count  .
    exists.count += number;
  };

  @action
  take = name => {
    const itemToTake = this.selectedItems.find(item => item.name === name);
    itemToTake.count--;
    if (itemToTake.count === 0) {
      //  0 
      this.selectedItems.remove(itemToTake); //  .
    }
  };

  @computed
  get total() {
    console.log(' ...');
    return this.selectedItems.reduce((previous, current) => {
      return previous + current.price * current.count;
    }, 0);
  }
}