import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';

//   .
//      props  .
//@inject('counter')
//   props       . 

//***          */
//@inject('word')

@inject(stores => ({
    author : stores.word.author,
    text : stores.word.text,
    handleAuthorChange : stores.word.handleAuthorChange,
    handleTextChange : stores.word.handleTextChange,
    putWord : stores.word.putWord
  }))

class GoodWordInput extends Component {
  render() {
      const { handleAuthorChange,
              handleTextChange,
              putWord,
              author,
                text} = this.props; 
     return (
      <div>
        <h3> Author </h3>
        <input onChange = {handleAuthorChange}/>
        <h3> Text </h3>
        <input onChange = {handleTextChange} />
        <button onClick = {() => putWord(author,text)} ></button>
      </div>
    );
  }
}

export default GoodWordInput;