import React, { Component } from 'react'
import style from './Notice.module.css'
import Popup from "reactjs-popup";
import Cookies from 'universal-cookie';
import Checkbox from '@material-ui/core/Checkbox';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import { styled } from '@material-ui/styles';
import Button from '@material-ui/core/Button';
import * as boardApi from '../../lib/api/boardApi'

const MyButton = styled(Button)({
  //background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
  background: 'linear-gradient(45deg, #fab005 30%, #f59f00 90%)',
  border: 0,
  borderRadius: 3,
  boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
  //boxShadow: '0 3px 5px 2px #ff',
  color: 'white',
  height: 30,
  padding: '0 10px',
});

const cookies = new Cookies();

export default class Notice extends Component { 
    state = {
        open: true ,
        checked :false,
        noticeContent : '',
        from : '',
        to : '',
    }
    componentDidMount(){
        this.getNotice();
    }

    getNotice = async() => {
       try{
            const response = await boardApi.getNotice();
            console.log(response)
            let noticeContent = response.data.result[0];
            console.log('noticeContent ',noticeContent)

            //  to    t
            //  > to  not visual


            this.setState({
                noticeContent : noticeContent.POSTS_NAEYONG,
                from : noticeContent.NOTICE_START,
                to : noticeContent.NOTICE_END
            })
          
        }catch(e){
            console.log(e)
        }
    }

    getCookie = () => {
        let result = cookies.get('modalNotice')
        if (result === 'end') {
            return false;
        }
        else {
            return true;
        }
    }
     setCookie = (  expiredays )=>{
        //console.log('setCookie ')
        let todayDate = new Date();
        todayDate.setDate( todayDate.getDate() + expiredays );
        //console.log('todayDate ', todayDate)
        cookies.set('modalNotice', 'end', { expires: todayDate } );
    }

    openModal = () =>{
        this.setState({ 
            open: true 
        })
    }
    closeModal = () =>{
        const { checked } = this.state;
        // this.setState({ 
        //     open: false 
        // })
        if(checked){
            this.setCookie(1);
        }

    }
    componentDidUpdate(){
        //console.log(this.state.checked)
    }
    handleChange = (e) => {
        this.setState({ 
            checked: !e.target.value
        });
      };
    

    render() {
        const { postsNaeYong } = this.props;
        const {noticeContent } = this.state;
    return (
      <div>
        <Popup
            open={this.state.open}
        >
        {close => (
          <div className={style.modal}>
            <a className={style.close} onClick={close}>
              &times;
            </a>
    
            <div className= {style.header}> 
                 
            </div>

            <div className= {style.content}>
                { ReactHtmlParser(noticeContent) }
         
            </div>
        
            <div className={style.actions}>
                {/*
              <Popup
                trigger={
                    <input type="checkbox" name="close" value="OK" onclick={() =>{
                        this.setState({
                            checked: !this.state.checked
                        })  
                    }}/>            
                }
                position="top center"
                closeOnDocumentClick>
                <span>
                  Lorem ipsum dolor sit amet, consectetur adipisicing elit. Beatae
                  magni omnis delectus nemo, maxime molestiae dolorem numquam
                  mollitia, voluptate ea, accusamus excepturi deleniti ratione
                  sapiente! Laudantium, aperiam doloribus. Odit, aut.
                </span>
              </Popup>

                */}
        
            <div className = {style.buttonFooter} >
                <div className = {style.checkBox} >
                    <Checkbox checked={this.state.checked} 
                              onChange={ () =>{
                                                this.setState({
                                                    checked: !this.state.checked
                                                })  
                                }} 
                                value="checked" />
                              
                </div>
           
                <div className = {style.closeBox}>
                    <MyButton
                        onClick={() => {
                            this.closeModal();
                            //console.log('modal closed ')
                            close()
                        }}>
                    </MyButton>
                </div>
            </div>
          </div>
          </div>
        )}
      </Popup>
      </div>
    )
  }
}
