export const get_pertitle_data_by_parameter = (flag, bitrate, cp, category, genre_org ) => {
    if( bitrate === null) { bitrate = "-1"}
    if( cp === null) { cp = "-1"}
    if( category === null) { category = "-1"}
    if( genre_org === null) { genre_org = "-1"}

   // console.log(bitrate , cp , category, genre_org)
    let data = { }

    if(flag == 1 ){
     data = {
        bitrate : bitrate
     }
    }

   // console.log(data)

    return (axios.post(cilentConfig.endpoint.api +'/bus/get_pertitle_data_by_parameter',data ))
}






