import React, { Component } from 'react'
import style from './WriteBoard.module.css'
import ReactQuill, { Quill, Mixin, Toolbar } from 'react-quill'; // ES6

import { Form, Input, Button } from 'reactstrap';
import history from '../../../history'
import 'react-quill/dist/quill.snow.css'; // ES6
import * as boardApi from '../../../lib/api/boardApi'
import Checkbox from '@material-ui/core/Checkbox';
import WithAuth from '../../WithAuth';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import DateTimePicker from '../DateTimePicker'
import moment from 'moment'
import _ from 'lodash'
import Editer from '../../Editer'

import './QuillModule.css'



/*  ,    */
class WriteBoard extends Component { 


       state = { 
            naeyong: '', // You can also pass a Quill Delta here
            jemok : '',
            checked: false,
            boardSelect : [],
            options : [],

            selectedOption : null,
            isSearchable : true,
            isClearable : true,


            to : '',
            from : ''
        } 
      

    //   static modules = {

    //     syntax: {
    //       highlight: text => hljs.highlightAuto(text).value,
    //     },
    //     toolbar: [
    //       [{container: "#toolbar"}],
    //       [{ 'font': Font.whitelist }],
    //       [{ size: [ 'small', false, 'large', 'huge' ]}],
    //       ['bold', 'italic', 'underline', 'blockquote'],
    //       [{ list: 'ordered' }, { list: 'bullet' }],
    //       ['link', 'image', 'video'],
    //       ['clean'],
    //       ['code-block'],
    //     ],
    //     clipboard: {
    //       matchVisual: false,
    //     },
    //   }
      
    //   static formats = [
    //     'header',
    //     'font',
    //     'size',
    //     'bold',
    //     'italic',
    //     'underline',
    //     'strike',
    //     'blockquote',
    //     'list',
    //     'bullet',
    //     'indent',
    //     'link',
    //     'image',
    //     'video',
    //     'code-block',
    //   ]



    
      handlePostsNameChange = (e) => {
        this.setState({
            jemok : e.target.value
        })
      }

      handleChange = (value) => {
        this.setState({ naeyong: value }, console.log('naeyong ', this.state.naeyong))
      }


    /*    */
     selectHandleChange = (selectedOption) => {
        this.setState({ selectedOption });
        console.log(`Option selected:`, selectedOption);
     }

     makeSelectOption = async() => {
        try{
            const getBoardList = await boardApi.getBoardList();
            let boardList = getBoardList.data;
            console.log(boardList)
            boardList = boardList.map((item) => {
                return {
                    value : item.BOARD_NUM,
                    label : item.BOARD_NAME
                }
            });
            boardList.unshift(
                {
                    value : 0,
                    label : ' '
                }
            )
            this.setState({
                options : boardList
            })
         }catch(e){
            console.log('error' , e )
         }

     }

     componentDidMount(){
       this.makeSelectOption();
     }
     


    /* daypicker   */
    pickUpFromTo = (thisTime) =>{
        this.setState({
            from : moment(thisTime)._d, 
            to :  moment(thisTime).add(7 ,'day')._d,
            defalut_from :  moment(thisTime)._d, 
            defalut_to : moment(thisTime).add(7, 'day')._d,
        })
    }
    handleFromChange = (from) => {
        this.setState({ 
            from,
        });
    }
    handleToChange = (to) => {
       this.setState({
        //from : moment(to).subtract('days', 1)._d,
        to , 
        });
    }
      /* BOARD_NUM, POSTS_NUM, MEM_IDX, POSTS_JEMOK, POSTS_NAEYONG */
      writePosts = async() => {
          const { jemok, 
                  naeyong,
                  checked,
                  from,
                  to,
                  selectedOption } = this.state;
         const { confirm } =this.props;

         console.log(selectedOption)
         if( _.isNull(selectedOption)){
            alert(' !')
            return;
         }
        if(selectedOption.BOARD_NUM === 0 ||
            _.isEmpty(selectedOption) ) {
             alert(' !')
             return;
        }
       moment(from).format('YYYY/MM/DD HH:mm:ss');

        try {
        
            const getPostsSequence = await boardApi.getPostsSequence();
            let sequence = getPostsSequence.data[0].SEQUENCE
            console.log(sequence)
            console.log('selectedOption.BOARD_NUM ', selectedOption.value)
            const response = await boardApi.writePosts(  selectedOption.value, 
                                                    parseInt(sequence), 
                                                    confirm.mem_idx, 
                                                    jemok,
                                                    naeyong, 
                                                    moment(from).format('YYYY/MM/DD 00:00:00'),
                                                    moment(to).format('YYYY/MM/DD 00:00:00'),
                                                    checked ? 'Y' : 'N');
            if (response.statusText === "OK") { //   
                history.push("/board/notice/" +`${sequence}`);
            }else{
                alert('')
            }
        }catch(e){
            console.log(e)
        }
      }
    
      

      render() {
        let { confirm } = this.props
        let { options, selectedOption, to , from}  = this.state; 
        return (
            <div className = {style.container}>
            
                <div>
                    <DateTimePicker pickUpFromTo = {this.pickUpFromTo}
                                    handleFromChange = {this.handleFromChange}
                                    handleToChange = {this.handleToChange}/>
                    <hr/>
                    <Select
                        className = {style.boardSelect}
                        value={selectedOption}
                        isSearchable ={this.state.isSearchable}
                        isClearable  = {this.state.isClearable}
                        onChange={this.selectHandleChange}
                        options={options}
                        placeholder ={' '}
                    />
       
                    <hr/>
                    <Form className ={style.jemokContainer}>
                        <Input className = {style.jemokItem}
                                onChange = {this.handlePostsNameChange} 
                                placeholder = ' .'/>
                    </Form>
                </div>
                <hr/>
                <div className = {style.editerContainer}>
                    <div className="text-editor">
                    {/*
                        <CustomToolbar/>
                        <ReactQuill value={this.state.naeyong}
                                    onChange={this.handleChange}
                                    modules={WriteBoard.modules}
                                    formats={WriteBoard.formats}
                                    />     */}
                        </div>
                        
                    <Editer confirm = {confirm} />

                                
                </div>
                <div>
                <Checkbox checked={this.state.checked} 
                onChange={ () =>{
                                  this.setState({
                                      checked: !this.state.checked
                                  })  
                  }} 
                  value="checked" />
                     
                </div>
        
                <div className ={style.buttonContainer}>
                    <Button color="primary"  onClick = {this.writePosts}> </Button>{' '}
                    <Link to={'/board/notice'}>
                        <Button color="secondary"></Button>{' '}
                    </Link>
                </div>
            </div>
        )
      }
    
}
export default WithAuth(WriteBoard)
