import React, { Component } from 'react'
import { Table } from 'reactstrap';
import BoardItem from '../BoardItem';
import * as boardApi from '../../../lib/api/boardApi'
import history from '../../../history'
import fontawesome from '@fortawesome/fontawesome'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit } from '@fortawesome/fontawesome-free-solid'
import moment from 'moment'
import style from './Board.module.css'
import ReactPaginate from 'react-paginate';

import Notice from '../../Notice'


import WithAuth from '../../WithAuth';
import StyledLink from '../../StyleComponent/StyledLink'
fontawesome.library.add(faEdit);
const PERPAGE =10;
class Board extends Component {
    state = {
        boardList : [],
        pageCount : 0,
        offset : 0,
        viewing : false,

    }
    componentDidMount(){
        this.getPostsList();
        this.getNoticeViewing();
    }

    getNoticeViewing = async() => {
        try{
            const response = await boardApi.getNoticeViewing();
            console.log(response)
            let resData = response.data;
            if(resData.code === 200){
                this.setState({
                    viewing : resData.result.viewing
                })
            }
            
        }catch(e){
            console.log(e)
        }
    }

    getPostsList = async() => {
        let { offset } = this.state;
        console.log("offset " , offset)
        try{
            const response =  await boardApi.getPostsList(offset);
            let postContent = response.data.posts;
            let meta = response.data.meta
            console.log(response)
            console.log(postContent)
            
            let boardList = postContent.map((item) =>{
                        return ({
                                postsNum : item.POSTS_NUM,
                                memUsername : item.MEM_USERNAME,
                                postsJemok : item.POSTS_JEMOK,
                                regDate : moment(item.REG_DATE).format('YYYY.MM.DD HH:MM'),
                                primeYn : item.PRIME_YN,
                                views: item.VIEWS,
                        })
            })

            // 
            if( boardList.length > 0 ){
                if ( boardList[0].primeYn !== 'Y' ){
                
                }
            }

            this.setState({
                boardList : boardList,
                pageCount: Math.ceil(meta.total_count / meta.limit),
            })
        

        }catch(e){
            console.log(e)
        }
    }
    
    handleWritePosts = () => {
        history.push("/board/notice/writePosts");
    }

    handlePageClick = data => {
        let selected = data.selected;
        let offset = Math.ceil(selected * PERPAGE);
        console.log(offset);
        this.setState({ offset: offset }, () => {
            this.getPostsList();
        });
      };

    scroll =() =>{
        window.scrollTo(500, 1000);
    }



    render() {
        console.log('this.state.viewing ', this.state.viewing)
        const { boardList, viewing } = this.state;

        
        const list = boardList.map((item, index) =>{
            return (
                    <BoardItem  key = {item.postsNum}
                                index = {index}
                                postsNum = {item.postsNum}
                                postsJemok = {item.postsJemok}
                                memUsername ={item.memUsername}
                                views = {item.views}
                                primeYn = {item.primeYn}
                                regDate = {item.regDate}
                                handleEditBoard = {this.handleEditBoard}
                            />
            
                )
            })

        return (
            <div>
                <button onClick ={this.scroll}>  </button>
                {viewing ? <Notice/> : '' }
                <StyledLink exact to= {'/board/writePosts'}>
                    <button className= {style.editButton}>
                        
                            <FontAwesomeIcon 
                                icon="edit" 
                                size="1x" 
                                color="white"
                            >
                            </FontAwesomeIcon>
                            
                    </button>
                </StyledLink>
                <Table responsive >
                    <thead>
                        <tr>
                            <th>#</th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {list}
                    </tbody>
                </Table>
                
                <div id = 'react-paginate'>
                    <ReactPaginate
                        previousLabel={'previous'}
                        nextLabel={'next'}
                        breakLabel={'...'}
                        breakClassName={'break-me'}
                        pageCount={this.state.pageCount}
                        marginPagesDisplayed={2}
                        pageRangeDisplayed={5}
                        onPageChange={this.handlePageClick}
                        containerClassName={'pagination'}
                        subContainerClassName={'pages pagination'}
                        activeClassName={'active'}
                    />
                </div>
           
            </div>
     )
  }
}
export default WithAuth(Board)