import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import StyledLink from '../../StyleComponent/StyledLink/'
import fontawesome from '@fortawesome/fontawesome'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheckCircle, faCheck } from '@fortawesome/fontawesome-free-solid'
import { Badge } from 'reactstrap';
import style from './Board.module.css'
fontawesome.library.add(faCheckCircle, faCheck);
export default class BoardItem extends Component {
  render() {

   const {  postsNum,
            postsJemok, 
            memUsername,
            regDate,
            primeYn,
            views,
            index,
              } = this.props;
    
     return (
          <tr className = {index === 0 && primeYn === 'Y' ? style.NoticePosts : ''}>
            <th scope="row" key= {postsNum}>{postsNum}</th>
            <td> 
              {index === 0  && primeYn === 'Y' ? 
              ( <Badge color="success"></Badge> ) :
              ('')
              }
              <StyledLink to ={"/board/notice/" + `${postsNum}`}> 
                {postsJemok} 
              </StyledLink> 
            </td>
            <td>{memUsername}</td>
            <td>{views}</td>
            <td>{regDate}</td>
            <td className = {style.tableItem}>
            {     
              primeYn === 'Y' ?  (
              <FontAwesomeIcon 
                icon="check" 
                size="1x" 
                color="green"
              >
              </FontAwesomeIcon>
            ) : ('')
            }
          </td>
          </tr>
    )
  }
}
