import React, { Component } from 'react'
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { Button, Tooltip  } from 'reactstrap';
import style from './PasswordChange.module.css';
import * as memberApi from '../../../lib/api/memberApi';
import WithAuth from '../../WithAuth';


const ERROR_STR = " ";
const EROOR_STR2 = " "; 
const styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
      width: 300,
    },
    dense: {
      marginTop: 19,
    },
    menu: {
      width: 200,
    },

  });

  
class PasswordChange extends Component {
    state = {
      passwordOrigin : '',
      passwordNewFirst : '',
      passwordNewSecond : '',

      showPasswordOrigin : false,
      showPasswordNewFirst : false,
      showPasswordNewSecond : false,

      tooltipOpenOrigin : false,
      tooltipOpenNewFirst : false,
      tooltipOpenNewSecond : false,

      open : false, //


      targetInput : 'originPasswordInput',

      msg : '',
    }
    handleClickShowPassword = () => {
      this.setState(state => ({ showPassword: !state.showPassword }));
    };
    // ,   6~20 
    checkValidationPassword = (password, res) =>{
        let reg_pwd = /^.*(?=.{6,20})(?=.*[0-9])(?=.*[a-zA-Z]).*$/;
        if(!reg_pwd.test(password)){
        return false;
        }
        return true;
    }

    updatePassword = async() => {
      const { confirm, toast } = this.props;
      const { checkValidationPassword,toolTipView } = this; 
      const { passwordOrigin,
              passwordNewFirst,
              passwordNewSecond } =  this.state;

      if (!checkValidationPassword(passwordOrigin)){
          toolTipView('tooltipOpenOrigin',
                      'originPasswordInput',
                      '    6~20 .',);

          return;
      }
      if (!checkValidationPassword(passwordNewFirst)){
        toolTipView('tooltipOpenNewFirst', 
                    'newFirstPasswordInput',
                    '    6~20 .',);
          return;
      }
      if (!checkValidationPassword(passwordNewSecond)){
        toolTipView('tooltipOpenNewSecond', 
                    'newSecondPasswordInput',
                    '    6~20 .');
          return;
      }
      if( passwordNewFirst !== passwordNewSecond){
        toolTipView('tooltipOpenNewFirst', 
        'newfirstPasswordInput',
        null );

        toolTipView('tooltipOpenNewSecond',
        'newSecondPasswordInput',
        null );


        toast.error('  .', {
          position: "top-center",
          autoClose: 1500,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          });
          return;
      }
      // api  
      //    ..
      
      try{
        console.log('try')
        const response = await memberApi.updatePassword( confirm.mem_email,
                                                         passwordOrigin,
                                                         passwordNewFirst, 
                                           passwordNewSecond);
        const resInfo = response.data;
        console.log(resInfo)
        if(resInfo.code === 400){
          toast.error("  .", {
            position: "top-center",
            autoClose: 1500,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            });
        }

        if(resInfo.code === 200){
          toast.success("  .", {
            position: "top-center",
            autoClose: 1500,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            });
            this.clearInput();
        }
        
      }catch(e){

        console.log(e)
        
      }
    }
    clearInput = () =>{
      const { passwordChangeSetting } = this.props;
      this.setState({
        passwordOrigin : '',
        passwordNewFirst : '',
        passwordNewSecond : '',
      },() => passwordChangeSetting())
    }



    handleChange = (e) =>{
      this.setState({
        [e.target.name]: e.target.value
      });
    }


    handleClickShowPasswordOrigin = () =>{
      this.setState({
        showPasswordOrigin : !this.state.showPasswordOrigin
      });
    }
    handleClickShowPasswordNewFirst = () =>{
      this.setState({
        showPasswordNewFirst : !this.state.showPasswordNewFirst
      });
    }
    handleClickShowPasswordNewSecond = () =>{
      this.setState({
        showPasswordNewSecond : !this.state.showPasswordNewSecond
      });
    }


    toolTipView = (tooltip, targetInput,  msg)=> {
      this.setState({
        [tooltip]: true,
        targetInput : targetInput,
        msg : msg,
        open : msg !== null ? true : false,
      },() =>{
          setTimeout(() => {
              this.setState({
                [tooltip]: false,
                targetInput : '',
                msg : '',
                open :false,
            })
          }, 2000);
      })
    }


    render() {
        const { tooltipOpenOrigin,
                tooltipOpenNewFirst,
                tooltipOpenNewSecond,
                open,
                msg,
                targetInput } = this.state;
        const { classes,
                passwordChangeSetting } = this.props;
        return (
        <div className = {style.passwdChangeContainer}>
            <form className={classes.container} noValidate autoComplete="off">
            <Tooltip 
                    innerClassName = {style.tooltip}
                    arrowClassName = {style.tooltip_success}  
                    placement="top" 
                    isOpen={open} 
                    //target="origin-password-input"
                    target={targetInput}
                    autohide = 'true'
                    >
              {msg}
            </Tooltip>
            <TextField
                    error = { tooltipOpenOrigin  ? (true) : (false)}
                    id="originPasswordInput"
                    label=" "
                    className={classes.textField}
                    type={this.state.showPasswordOrigin ? 'text' : 'password'}
                    autoComplete="current-password"
                    margin="normal"
                    name = 'passwordOrigin'
                    onChange ={this.handleChange}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="Toggle password visibility"
                            onClick={this.handleClickShowPasswordOrigin}
                            id = "passwordOrigin"
                          >
                            {this.state.showPasswordOrigin ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                />
            
            </form>
            <form className={classes.container} noValidate autoComplete="off">
              <TextField
                    error = { tooltipOpenNewFirst  ? (true) : (false)}
                    id="newFirstPasswordInput"
                    label=" "
                    className={classes.textField}
                    type={this.state.showPasswordNewFirst ? 'text' : 'password'}
                    autoComplete="current-password"
                    margin="normal"
                    name = 'passwordNewFirst'
                    onChange ={this.handleChange}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="Toggle password visibility"
                            onClick={this.handleClickShowPasswordNewFirst}
                          >
                            {this.state.showPasswordNewFirst ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                />
            </form>
            <form className={classes.container} noValidate autoComplete="off">
                <TextField
                    error = {tooltipOpenNewSecond  ? (true) : (false)}
                    id="newSecondPasswordInput"
                    label="  "
                    className={classes.textField}
                    type={this.state.showPasswordNewSecond ? 'text' : 'password'}
                    autoComplete="current-password"
                    margin="normal"
                    name = 'passwordNewSecond'
                    onChange ={this.handleChange}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="Toggle password visibility"
                            onClick={this.handleClickShowPasswordNewSecond}
                          >
                            {this.state.showPasswordNewSecond ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                />
            </form>
            <div className>
              <Button outline color="primary"
                      onClick ={this.updatePassword} > 
                 
              </Button>{' '}
              <Button outline color="info"
                       onClick ={passwordChangeSetting} >
                
              </Button>{' '}
            </div>
        </div>
        )
  }
}
PasswordChange.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default WithAuth(withStyles(styles)(PasswordChange));
