import React, { Component } from 'react'
import style from './EditBoard.module.css'
import ReactQuill, { Quill, Mixin, Toolbar } from 'react-quill'; // ES6
import { Form, Input, Button } from 'reactstrap';
import history from '../../../history'
import 'react-quill/dist/quill.snow.css'; // ES6
import * as boardApi from '../../../lib/api/boardApi'
import Checkbox from '@material-ui/core/Checkbox';
import WithAuth from '../../WithAuth';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import DateTimePicker from '../DateTimePicker'
import moment from 'moment'
/*  ,    */
class EditBoard extends Component {

    constructor(props) {
        super(props)
        this.state = { 
            postsNaeyong: '', // You can also pass a Quill Delta here
            postsJemok : '',
            editNumber: '',
            checked : '',

            boardNum : [],
            boardSelect : [],
            options : [],

            selectedOption : null,
            isSearchable : true,
            isClearable : true,

            to : '',
            from : ''
            
      }
    }
    
    makeSelectOption = async() => {
        try{
            const getBoardList = await boardApi.getBoardList();
            let boardList = getBoardList.data;
            console.log(boardList)
            boardList = boardList.map((item) => {
                return {
                    value : item.BOARD_NUM,
                    label : item.BOARD_NAME
                }
            });
            boardList.unshift(
                {
                    value : 0,
                    label : ' '
                }
            )

            //* boardNum     */
            let selectedOption = boardList.filter((item) => item.value === this.state.boardNum)

            this.setState({
                options : boardList,
                selectedOption: selectedOption
            })
         }catch(e){
            console.log('error' , e )
         }
     }

    /*    */
     selectHandleChange = (selectedOption) => {
 
        this.setState({ selectedOption });
        console.log(`Option selected:`, selectedOption);
     }


     /*    */
      handlePostsJemokChange = (e) => {
        this.setState({
            postsJemok : e.target.value
        })
      }

      handleChange = (value) => {
        this.setState({ postsNaeyong: value }, console.log('postsNaeyong ', this.state.postsNaeyong))
      }

      handleCancle = () => {
        const { editNumber } = this.state; 
        history.push(("/board/notice/" +`${editNumber}`))
      }
      

      /*    */
      ifPostsExist = async() =>{
        //console.log('posts')
        const { match } = this.props;
        let editNumber = match.params.editNumber;
        //console.log("editNumber ", editNumber )
        try {
            const ifExistRes = await boardApi.ifPostsExist(editNumber);
            let ifExist = ifExistRes.data[0].IFEXIST;
            if ( ifExist === 1){
                // \
                this.setPosts();
            }else{
                alert(' .')
                history.push("/board/notice");
            }
        }catch(e){
            console.log(e)
        }
      }

      componentDidMount(){
          this.ifPostsExist()
      }


      setPosts = async() =>{
        const { match } = this.props;
        const response = await boardApi.getPosts(match.params.editNumber);
        console.log(response)
        let postsContent = response.data[0];
        let boardNum = postsContent.BOARD_NUM;
        let postsJemok = postsContent.POSTS_JEMOK;
        let postsNaeyong = postsContent.POSTS_NAEYONG;
        let postsPrimeYN = postsContent.PRIME_YN;
        let noticeStart = postsContent.NOTICE_START;
        let noticeEnd = postsContent.NOTICE_END;
        console.log('noticeStart ', moment(noticeStart)._d)
        console.log('noticeEnd ', noticeEnd)
        this.setState({
            boardNum : boardNum,
            postsJemok : postsJemok,
            postsNaeyong : postsNaeyong,
            editNumber : match.params.editNumber,
            checked : postsPrimeYN === 'Y' ? true : false,
            from : moment(noticeStart)._d,
            to :moment(noticeEnd)._d
        },() => this.makeSelectOption())
      }

      editPosts = async() => {

          const { editNumber,
                  postsJemok, 
                  postsNaeyong,
                  checked,
                   from,
                   to }=this.state;
         const { match } = this.props;
        try {
            const response = await boardApi.editPosts(1, 
                parseInt(editNumber) , 
                postsJemok, 
                postsNaeyong, 
                checked ? 'Y' : 'N',
                moment(from).format('YYYY/MM/DD 00:00:00'),
                moment(to).format('YYYY/MM/DD 00:00:00'),
                );
            //console.log(response)
            if (response.statusText === "OK") { //   
                history.push("/board/notice/" +`${editNumber}`);
            }
        }catch(e){
            console.log(e)
        }

      }


    /* daypicker   */
    pickUpFromTo = (thisTime) =>{
        this.setState({
            from : moment(thisTime)._d, 
            to :  moment(thisTime).add(7 ,'day')._d,
            defalut_from :  moment(thisTime)._d, 
            defalut_to : moment(thisTime).add(7, 'day')._d,
        })
    }

    buttonClickDay = (beforeDate, afterDate) =>{
        console.log(beforeDate , afterDate)
        this.setState({
            from :  moment(beforeDate)._d,
            to : moment(afterDate)._d,
        })
    }
    handleFromChange = (from) => {
        this.setState({ 
            from,
        });
    }
    handleToChange = (to) => {
       this.setState({
        //from : moment(to).subtract('days', 1)._d,
        to , 
        });
    }
    
 
      render() {
        let { editNumber,
            postsJemok, 
            postsNaeyong,
            from,
            to,
            selectedOption,
            options } = this.state;

      
        return (
            <div className = {style.container}>
                <div>
                    <DateTimePicker pickUpFromTo = {this.pickUpFromTo}
                                    handleFromChange = {this.handleFromChange}
                                    handleToChange = {this.handleToChange}
                                    buttonClickDay = {this.buttonClickDay}
                                    from = {from}
                                    to = {to}
                                    edit = {true}
                                    />
                    <hr/>
                    <Select
                        className = {style.boardSelect}
                        value={selectedOption}
                        isSearchable ={this.state.isSearchable}
                        isClearable  = {this.state.isClearable}
                        onChange={this.selectHandleChange}
                        options={options}
                        placeholder ={' '}
                    />

                    <hr/>
                    <Form className ={style.jemokContainer}>
                        <Input className = {style.jemokItem}
                                onChange = {this.handlePostsJemokChange}
                                value = {postsJemok}
                                placeholder = ' .' />
                    </Form>
                </div>
                <hr/>
                <div className = {style.editerContainer}>
                    <ReactQuill value={postsNaeyong}
                                onChange={this.handleChange} />
                </div>
                <div>
                <Checkbox checked={this.state.checked} 
                onChange={ () =>{
                                  this.setState({
                                      checked: !this.state.checked
                                  })  
                  }} 
                  value="checked" />
                     
                </div>
                <div >
                    <Button color="primary"  onClick = {this.editPosts}> </Button>{' '}
                    <Link to={"/board/notice/" +`${editNumber}`}>
                        <Button color="secondary"></Button>{' '}
                    </Link>
                </div>
            </div>
        )
      }
    
}
export default WithAuth(EditBoard)
