import React, { Component } from 'react'
import style from './Prompt.module.css'
import Popup from "reactjs-popup";
import { styled } from '@material-ui/styles';
import Button from '@material-ui/core/Button';

const MyButton = styled(Button)({
  //background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
  background: 'linear-gradient(45deg, #fab005 30%, #f59f00 90%)',
  border: 0,
  borderRadius: 3,
  boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
  //boxShadow: '0 3px 5px 2px #ff',
  color: 'white',
  height: 33,
  padding: '0 30px',
});

const MyButton2 = styled(Button)({
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    border: 0,
    borderRadius: 3,
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    color: 'white',
    height: 33,
    padding: '0 30px',
  });
  
  

export default class Prompt extends Component { 
    state = {
        open: false ,
        checked :false
    }
    componentDidMount(){
    
    }

    openModal = () =>{
        this.setState({ 
            open: true 
        })
    }
    closeModal = () =>{
        const { checked } = this.state;
    }

    handleChange = (e) => {
        this.setState({ 
            checked: !e.target.value
        });
    };

    
    

    render() {
        const  { open, confirmDelete, closeModal } = this.props;
    return (
      <div>
        <Popup
            open={open}
            onClose = {closeModal}
        >
        {close =>  (
          <div className={style.modal}>
            <a className={style.close} onClick={close}>
              &times;
            </a>
    
            <div className= {style.header}> 
                 
            </div>

            <div className= {style.content}>
                <p>  ?</p>
                <p>  </p>
            </div>
        
            <div className={style.actions}>
                {/*
              <Popup
                trigger={
                    <input type="checkbox" name="close" value="OK" onclick={() =>{
                        this.setState({
                            checked: !this.state.checked
                        })  
                    }}/>            
                }
                position="top center"
                closeOnDocumentClick>
                <span>
                  Lorem ipsum dolor sit amet, consectetur adipisicing elit. Beatae
                  magni omnis delectus nemo, maxime molestiae dolorem numquam
                  mollitia, voluptate ea, accusamus excepturi deleniti ratione
                  sapiente! Laudantium, aperiam doloribus. Odit, aut.
                </span>
              </Popup>

              */}
        
            <div className = {style.buttonFooter} >
                <div className = {style.checkBox} >
                    <MyButton2
                    onClick = {confirmDelete}
            
                    ></MyButton2>
                </div>
           
                <div className = {style.closeBox}>
                    <MyButton
                        onClick={() => {
                            this.closeModal();
                            closeModal()
                            console.log('modal closed ')
                            close()
                        }}>
                    </MyButton>
                </div>
            </div>
          </div>
          </div>
        )}
      </Popup>
      </div>
    )
  }
}
