import React, { Component } from 'react'
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import './BoardCardItem.scss'
import _ from 'lodash'
import StyledLink from '../../StyleComponent/StyledLink/'
export default class BoardCardItem extends Component {

  gettext = () =>{
    let viewText = ''
    if( !_.isNil(this.props.postsNaeYong)){
       viewText = this.props.postsNaeYong.replace(/<(\/)?([a-zA-Z]*)(\s[a-zA-Z]*=[^>]*)?(\s)*(\/)?>/ig, "");
    }
    return viewText
  }


  render() {
    const {
      editDate, 
      memUsername, 
      postsJemok, 
      postsNaeYong,
      thumbnail,
      postsNum,
      primeYn,
      regDate,
      views,} = this.props;

    return (
      <div className ='article-wrapper'>

        <div className ='article'>
          <div className ='article-header'> 
          <StyledLink to ={"/board/article/" + `${postsNum}`}>   
              <a className ='article-thumbnail'>
                <img src = {thumbnail}/>
                {/*<img src="https://thumb.velog.io/resize?url=https://images.velog.io/post-images/godori/967f0f60-698f-11e9-96b4-3faf0bb6ff50/dockercheatsheet2.png&amp;width=512" alt=" Docker Cheat Sheet"/>*/}
              </a> 
            </StyledLink>
          </div>
        
          <div className = ''>
          
          <div className = 'content-wrapper'>
            <div className = 'content-header'>
              <div className ='content-title'>
                {postsJemok}
              </div>
              <div className ='content-writer'>
                 {memUsername}
              </div>
              <div className ='content-date'>
                 {regDate} 
              </div>
              <div className ='content-view'>
                 {views}
              </div>
             </div>
            <div className ='content-content'> 
              {this.gettext()}
            </div>
            <div className ='article-footer'> 
              
            </div>
          </div>
          </div>

        </div>
      </div>
    )
  }
}
