import React from 'react';
import BoardCardItem from '../BoardCardItem'
import { observer, inject, } from 'mobx-react'
import { Container, Row, Col } from 'reactstrap';
import StyledLink from '../../StyleComponent/StyledLink/'

const PERPAGE = 10;
let renderCount = 1; 
class BoardCard extends React.Component {
  componentDidMount(){
    const { getPostsList } = this.props;
    getPostsList();
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    //  ,   
    window.removeEventListener("scroll", this.handleScroll);
    this.props.setInitForInfScroll();
  
  }
  
  handleScroll = () => {
    const { isLast, isLoading, renderCount} = this.props;
    const { innerHeight } = window;
    const { scrollHeight } = document.body;
    // IE document.documentElement  .
    const scrollTop =
      (document.documentElement && document.documentElement.scrollTop) ||
      document.body.scrollTop;
    //  ,    100   .
    if (scrollHeight - innerHeight - scrollTop < 100) {
      console.log("Almost Bottom Of This Browser");
      if( !isLast && !isLoading){
        this.getOffset();
      }
    }
  };

  getOffset = () => { 
    const {setOffset, getPostsListForInf, renderCount} = this.props; 
    let offsetnow = Math.ceil(renderCount * PERPAGE);
    console.log(offsetnow);
    setOffset(offsetnow);
    getPostsListForInf();
  }

  

  getOrderRandom = () => {
    let random = Math.floor(Math.random() * 10) + 1;
    return random;
  }

  render() {
    let { boardList } = this.props;

    let frontList = boardList.slice(0,2) //  
    if( boardList.length > 2){
      boardList = boardList.slice(2,boardList.length);
    }

    /* board    */
    let allBoardList = [] ;   
    let sliceBoardList = [];
    boardList.map((item) => {
      sliceBoardList.push(item);
      if( sliceBoardList.length % 3 === 0 ){
        allBoardList.push(sliceBoardList)
        sliceBoardList = [];
      }
    })
    if( sliceBoardList.length !== 0){
      allBoardList.push(sliceBoardList);
    }
  
    console.log("allBoardList ", allBoardList)

    let sizeList = [3, 4, 5]; 
    let idx = 0;
    frontList = frontList.map((item,key) => {
      console.log('item ', item.EDIT_DATE)
      return (
        <Col sm={{ size:  6, 
                    order: key, 
                    offset: 0 }}>
            <BoardCardItem      key = {item.postsNum}
                                index = {key}
                                postsNum = {item.postsNum}
                                memUsername = {item.memUsername}
                                postsJemok = {item.postsJemok}
                                postsNaeYong = {item.postsNaeYong}
                                thumbnail = {item.thumbnail}
                                views = {item.views}
                                primeYn = {item.primeYn}
                                regDate = {item.regDate}
                            />
        </Col>
      )
    })

    let renderList = allBoardList.map((sliceBoardList) => {
        return  ( 
          <Row>
          {
            sliceBoardList.map((item, key) =>{
              if( idx === sizeList.length ){
                idx = 0;
              }
              console.log('idx ', idx)
              return (
                        <Col sm={{ size: sizeList[idx++], 
                                   order: this.getOrderRandom(), 
                                   offset: 0 }}>
                          <BoardCardItem key = {item.postsNum}
                                        index = {key}
                                        postsNum = {item.postsNum}
                                        memUsername = {item.memUsername}
                                        postsJemok = {item.postsJemok}
                                        postsNaeYong = {item.postsNaeYong}
                                        thumbnail = {item.thumbnail}
                                        views = {item.views}
                                        primeYn = {item.primeYn}
                                        regDate = {item.regDate}/>
                        </Col>
                  
              )
            })
          }
          </Row>
        ) 
    })
    
    return (
      <Container>
        <Row>
          {frontList}
        </Row>
          {renderList}
      </Container>
      
    );
  }
}
export default inject(({ board }) => ({
  offset : board.offset,
  isLoading : board.isLoading,
  isLast : board.isLast,
  renderCount : board.renderCount,
  setInitForInfScroll : board.setInitForInfScroll,
  getPostsListForInf : board.getPostsListForInf,
  setOffset : board.setOffset,
  getPostsList : board.getPostsList,
  boardList : board.boardList
  
}))(observer(BoardCard));
